<template>
    <div class='shoucang'>
        <div class="list">
            <div class="zanwu" v-if="user_collect.length<=0">
                <img src="../../../assets/img/shangcheng/tubiao.png" alt="">
                <h1>暂无收藏商品...</h1>
            </div>
            <div class="item" v-for="(item,i) in user_collect" v-else>
                <div class="shanchu" @click="quxiao(item.goods_id)"></div>
                <div class="img">
                    <img :src="imgUrl + item.thumb" alt="">
                </div>
                <div class="flex">
                    <h1>{{item.goods_name}}</h1>
                    <p>¥{{item.sell_price}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            activeName:'first',
            user_collect:'',//收藏列表
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.getshoucang()
    },
    watch:{
    },
    methods:{
        //获取收藏列表
        getshoucang(){
            var _this = this
            _this.apis.get(_this.apis.user_collect,{params:{lang:_this.apis.lang}}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.user_collect = res.data.data
                }
            })
        },
        //取消收藏
        quxiao(id){
            var _this = this;
            console.log(1)
            _this.apis.get(_this.apis.collect,{params:{lang:_this.apis.lang,goods_id:id}}).then(function(res) {
                console.log(res.data)
                if(res.data.code == 0 ){
                    _this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    _this.getshoucang()
                }else{
                    _this.$message({
                        message: res.data.msg,
                        type: 'info'
                    });
                }
            })
        }
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.shoucang{
    .zanwu{
        margin: 100px auto;
        text-align: center;
        img{
            width: 338px;
            height: 306px;
        }
        h1{
            color: #b6b6b6;
            font-size: 16px;
            line-height: 50px; 
            margin: 10px 0;
            font-weight: 100;
        }
    }
    .list{
        min-height: 631px;
        margin-bottom: 50px;
        display: flex;
        justify-content: left;
        flex-wrap: wrap;
        .item{
            padding: 13px;
            position: relative;
            .shanchu{
                width: 24px;
                height: 24px;
                background-image: url(../../../assets/img/shangcheng/shanchu.png);
                position: absolute;
                top: 14px;
                right: 14px;
            }
            .img{
                width: 230px;
                height: 285px;
            }
            .flex{
                text-align: center;
                h1{
                    
                }
                p{
                    color: #FE4200;
                }
            }
        }
    }
}
</style>


